:root {

	--primary: #0d6efd;
	--warning: #ffc107;
	--success: #198754;
	--danger: #e62b1e;

	--zen-bg: #212529bf;
}


@font-face {
	font-family: Helvetica;
	src:
		url('../fonts/Helvetica.ttf') format('ttf'),
		url('../fonts/Helvetica.woff2') format('woff2'),
		url('../fonts/Helvetica.woff') format('woff');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: Helvetica-Bold;
	src:
		url('../fonts/Helvetica-Bold.ttf') format('ttf'),
		url('../fonts/Helvetica-Bold.woff2') format('woff2'),
		url('../fonts/Helvetica-Bold.woff') format('woff');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: Helvetica-Light;
	src:
		url('../fonts/Helvetica-Light.ttf') format('ttf'),
		url('../fonts/Helvetica-Light.woff2') format('woff2'),
		url('../fonts/Helvetica-Light.woff') format('woff');
	font-weight: normal;
	font-style: normal;
}

body {
	font-family: Helvetica;
	overflow-y: scroll;
}

.handwritten {
	font-family: handwritten;
}

.ultra-text {
	font-weight: bold !important;
	-webkit-text-fill-color: transparent !important;
	-webkit-background-clip: text !important;
	background-clip: text !important;
}

.bg-sea {
	background-image: linear-gradient(to right, var(--dark-color), var(--light-color));
}

.bg-sky {
	background-image: linear-gradient(70deg, #a2facf 0%, #64acff 100%);
}

.bg-sun {
	background-image: linear-gradient(to right, var(--golden-orange), var(--golden-gold));
}

.animated-underline:hover:after {
	width: 100%;
}

.bg-sun.animated-underline:hover:after {
	background-image: linear-gradient(to right, var(--golden-orange), var(--golden-gold));
}

.bg-sea.animated-underline:hover:after {
	background-image: linear-gradient(to right, var(--dark-color), var(--light-color));
}


div#alert-box {
	z-index: 999;
	top: 0%;
	position: fixed;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.5);
}

.overscroll-contain {
	overscroll-behavior: contain;
}

