.loading-waves {
	overflow: hidden;
	position: relative;
	background: white;
}

.loading-waves::after {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	animation: waves 1.6s linear .5s infinite;
	background: linear-gradient(90deg, #a2a2a200, var(--golden-orange), transparent)
}

@keyframes waves {
	0% {
		transform: translateX(-100%);
	}

	60% {
		transform: translateX(100%);
	}

	100% {
		transform: translateX(100%);
	}
}

