.kaoukab-check+label {
	cursor: pointer;
	display: block;
	width: 100%;
	padding: 0.375rem 0.75rem;
	padding-left: calc(1.5em + 0.75rem);
}

.kaoukab-check:checked+label {
	background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23e65c00' d='M2.3 6.73L.6 4.53c-.4-1.04.46-1.4 1.1-.8l1.1 1.4 3.4-3.8c.6-.63 1.6-.27 1.2.7l-4 4.6c-.43.5-.8.4-1.1.1z'/%3e%3c/svg%3e");
	background-repeat: no-repeat;
	background-position: left calc(0.375em + 0.1875rem) center;
	background-size: calc(0.75em + 0.375rem) calc(0.75em + 0.375rem);
}

.text-nowrap-a {
	overflow: hidden;
	display: grid;
}

.text-nowrap-a * {
	white-space: nowrap;
	text-overflow: ellipsis;
}

.pulsing-filter {
	animation: pulsing-filter-animation .5s infinite;
}

@keyframes pulsing-filter-animation {
	from {
		filter: drop-shadow(0px 0px 20px var(--golden-orange));
	}

	25% {
		filter: drop-shadow(0px 0px 1px var(--golden-orange));
	}

	75% {
		filter: drop-shadow(0px 0px 1px var(--golden-orange));
	}

	to {
		filter: drop-shadow(0px 0px 20px var(--golden-orange));
	}
}


