.range-progress {
	overflow: hidden;
	-webkit-appearance: none;
	width: 100%;
	height: 4px;
	background: #d3d3d3;
	opacity: 0.7;
	border-radius: 10px;
	outline: none;
	-webkit-transition: .2s;
	transition: opacity .2s;
}

.range-progress::-webkit-slider-thumb {
	-webkit-appearance: none;
	appearance: none;
	width: 0px;
	height: 0px;
	border-radius: 50%;
	cursor: pointer;
}

.range-progress.orange::-webkit-slider-thumb {
	box-shadow: -80px 0 0 80px var(--golden-orange);
}

.range-progress.primary::-webkit-slider-thumb {
	box-shadow: -80px 0 0 80px var(--bs-primary);
}

.range-progress::-moz-range-thumb {
	width: 8px;
	height: 8px;
	border-radius: 50%;
	background: var(--golden-orange);
	cursor: pointer;
}

/** FF*/
input[type="range"]::-moz-range-progress {
	background-color: var(--golden-orange);
}

input[type="range"]::-moz-range-track {
	background-color: var(--background);
}

/* IE*/
input[type="range"]::-ms-fill-lower {
	background-color: var(--golden-orange);
}

input[type="range"]::-ms-fill-upper {
	background-color: var(--background);
}