html {
	scroll-behavior: smooth !important;
}


@keyframes header-picture {
	0% {
		background-position: 0% 50%;
	}

	50% {
		background-position: 100% 50%;
	}

	100% {
		background-position: 0% 50%;
	}
}

#canvas {
	z-index: 1;
}

@keyframes ligthtodark {
	from {
		filter: drop-shadow(0px 0px 6px var(--light-color));
	}

	to {
		filter: drop-shadow(0px 0px 6px var(--dark-color));
	}
}

@keyframes darktolight {
	from {
		filter: drop-shadow(0px 0px 6px var(--dark-color));
	}

	to {
		filter: drop-shadow(0px 0px 6px var(--light-color));
	}
}


@media (min-width: 992px) {
	.index-grid {
		grid-template-columns: 1fr 1fr;
	}
}

.index-grid {
	display: grid;
	gap: 20px;
}


.ghost-scrollbar {
	overflow: overlay;
}

.ghost-scrollbar::-webkit-scrollbar {
	width: 10px;
	height: 10px;
}

.ghost-scrollbar::-webkit-scrollbar-thumb {
	background: rgba(90, 90, 90);
}

.ghost-scrollbar::-webkit-scrollbar-track {
	background: rgba(0, 0, 0, 0.2);
}

.rotating {
	transition: transform 3s;
}

.rotating.show {
	animation: rotatingAnimation;
	animation-duration: 3s;
}

.rotating.hide {
	animation: rotatingAnimation;
	animation-duration: 3s;
}

@keyframes rotatingAnimationShow {
	form {
		transform: rotate3d(2, 0, 0, 90deg);
	}

	to {
		transform: rotate3d(2, 0, 0, 90deg);
	}
}

@keyframes rotatingAnimationHide {
	form {
		transform: rotate3d(2, 0, 0, 90deg);
	}

	to {
		transform: rotate3d(2, 0, 0, 90deg);
	}
}

.sky-shadow {
	/* box-shadow: -20px -20px 20px 15px #8653ef63, 
	-15px -15px 15px 10px #dd53ef63, 
	-10px -10px 10px 5px #3600bd63, 
	-5px -5px 5px 0px #00aeff63; */
	border: 1px solid #00a6ff96;
}

.purple-circle {
	transition: right 1.05s, left 1.05s, opacity 1.05s, transform .75s;
}

.purple-circle::before {
	content: ' ';
	border-radius: 50%;
	width: 250px;
	filter: blur(10px);
	height: 250px;
	transform: translate(-50%, 50%);
	position: absolute;
	background: radial-gradient(#e65c004f 1%, #e65c003d 15%, #e65c000f 15%, transparent 1000%);
	z-index: 105;
}



.slide {
	transition: right 1.05s, left 1.05s, opacity .5s;
}

.slide.right.show {
	opacity: 1;
}

.slide.right.show {
	right: 0;
}

.slide.right.show.replace {
	left: -100%;
}

.slide.right.hide {
	opacity: 0;
}

.slide.right.hide:not(.replace) {
	right: -100%;
}

.slide.right.hide.replace {
	left: 0;
}

.slide.bottom.show {
	opacity: 1;
	bottom: 0;
}

.slide.bottom.hide {
	opacity: 0;
	bottom: -100%;
}

.slide.left.show {
	left: 0;
	opacity: 1;
}

.slide.left.hide {
	opacity: 0;
	left: -100%;
}


.index-pic-container {
	position: relative;
	height: 500px;
}

.index-pic-container>* * {
	position: absolute;
	top: 1rem;
	width: 90%;
}

.index-pic-container:hover * {
	transform: scale(1.1);
}

.red-circle-arrow {
	position: absolute;
	top: 2.5%;
	left: 44%;
	z-index: 10;
}

.red-circle-arrow svg {
	stroke-dasharray: 1250;
	stroke-dashoffset: 1250;
	transition: all 1s ease;
}

.red-circle-arrow.show svg {
	stroke-dashoffset: 0;
}



.menu-btn {
	cursor: pointer;
	font-weight: bold !important;
	-webkit-text-fill-color: transparent !important;
	-webkit-background-clip: text !important;
	background-clip: text !important;
	background: #fff;
}

.menu-btn:hover {
	background: linear-gradient(var(--bs-danger), var(--bs-danger)) #fff no-repeat 0 0;
	background-size: 0 100%;
	animation: shine .25s linear forwards;
}

#landing-title {
	font-size: 1.25rem;
}

.chapelle-pic {
	max-width: 100%;
}

@media (min-width: 576px) {
	.chapelle-pic {
		max-width: 90%;
	}

	#landing-title {
		font-size: 1.5rem;
	}
}

@media (min-width: 768px) {
