.chapelle-pic {
	max-height: 80%;
	z-index: 1;
}

.animated-underline:after {
	content: '';
	display: block;
	height: 3px;
	width: 0;
	background: transparent;
	transition: width .5s ease, background-color .5s ease;
}

.animated-underline:hover:after {
	background: #FFF;
}

@keyframes shine {
	0% {
		background-size: 0%;
	}

	100% {
		background-size: 100%;
	}
}


.glass-card {
	background: rgba(255, 255, 255, 0.65);
	box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
	backdrop-filter: blur(4px);
	-webkit-backdrop-filter: blur(4px);
	border: 1px solid rgba(255, 255, 255, 0.18);
}

.glass-card.clickable {
	cursor: pointer;
}

.glass-card.clickable:hover:not(.active) {
	background: rgb(255 255 255 / 85%);
}

.red-square {
	height: 7rem;
	background-color: #e62b1e;
	content: "";
	left: 0;
	position: absolute;
	-webkit-transform-origin: right;
	-o-transform-origin: right;
	transform-origin: right;
	width: 100%;
}

.red-square.open {
	-o-transform: skewY(3deg);
	transform: skewY(3deg);
	-webkit-transform: skewY(3deg);
}

.red-square.close {
	-o-transform: skewY(-3deg);
	transform: skewY(-3deg);
	-webkit-transform: skewY(-3deg);
}

.opacity {
	transition: opacity .15s linear;
}

.opacity.hide {
	opacity: 0
}

.opacity.show {
	opacity: 1
}

.sponsor-logo:hover {
	box-shadow: 0 0.5rem 1rem rgb(0 0 0 / 15%) !important;
}


.sponsor-logo>img {
	max-height: 50%;
	max-width: 100%;
	margin: auto;
}

.left-hidden {
	transition: left .5s linear;
}

.left-hidden.show {
	left: 0%
}

.left-hidden.hide {
	left: -75%
}

.right-hidden {
	transition: right .5s linear;
}

.right-hidden.show {
	right: 0%
}

.right-hidden.hide {
	right: -75%
}

.timeline-card {
	transition: opacity .5s linear, right .5s linear, left .5s linear;
}

.timeline-card.left.hide {
	opacity: 0;
	left: -75%
}

.timeline-card.left.show {
	opacity: 1;
	left: 0%
}

.timeline-card.right.hide {
	opacity: 0;
	right: -75%
}

.timeline-card.right.show {
	opacity: 1;
	right: 0%
}

.speaker-badge.active {
	background: rgb(230 43 30 / 50%);
	color: #fff;
}

.speaker-badge:hover {
	color: #000;
}